<template>
  <div id="menuarea" class="border">
    <b-container fluid>
      <b-form-row no-gutters>
        <b-col>
          <b-button block variant="outline-primary" @click="setSubMenu(0)">天氣概況</b-button>
        </b-col>
        <b-col>
          <b-button block variant="outline-primary" @click="setSubMenu(1)">雨量</b-button>
        </b-col>
        <b-col>
          <b-button block variant="outline-primary" @click="setSubMenu(2)">水位</b-button>
        </b-col>
      </b-form-row>
      <b-form-row>
        <b-col>
          <b-button block variant="outline-primary" @click="setSubMenu(3)">淹水感測器</b-button>
        </b-col>
        <b-col>
          <b-button block variant="outline-primary" @click="setSubMenu(4)">即時監控</b-button>
        </b-col>
        <b-col>
          <b-button block variant="outline-primary" @click="setSubMenu(5)">防汛工具</b-button>
        </b-col>
      </b-form-row>
      <b-form-row>
        <b-col>
          <b-button block variant="outline-primary" @click="setSubMenu(6)">歷史資料</b-button>
        </b-col>
        <b-col>
          <b-button block variant="outline-primary" @click="setSubMenu(7)">相關連結</b-button>
        </b-col>
        <b-col>
          <b-button block variant="outline-primary" @click="setSubMenu(8)">系統管理</b-button>
        </b-col>
      </b-form-row>
    </b-container>
    <div v-if="hasB1Menu">
      <hr />
      <b-list-group horizontal>
        <b-list-group-item button v-for="(item, i) in b1Menu" :key="i" @click="onB1ItemClick(item)">
          <font-awesome-icon v-if="item.state === true" icon="check-square" size="lg" />
          <font-awesome-icon v-if="item.state === false" far icon="square" size="lg" />
          <font-awesome-icon v-if="item.state === undefined" icon="search-plus" size="lg" />
          {{item.name}}
        </b-list-group-item>
      </b-list-group>
    </div>
    <div v-if="hasB2Menu">
      <hr />
      <b-list-group flush class="menuItem">
        <menu-list v-for="(child, i) in b2Menu" :key="i" :item="child" @showPage="onShowPage"></menu-list>
      </b-list-group>
    </div>
    <b-modal id="pageModal" size="xl" :title="pageTitle" scrollable centered :hide-footer="true">
      <router-view />
    </b-modal>
  </div>
</template>
<style scoped>
#nav {
  padding: 30px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
}

#menuarea {
  padding: 5px;
  overflow: auto;
}

.menuItem {
  text-align: left;
}
</style>

<script>
import MenuList from "@/views/MenuList";
import router from "../router";
import menu1 from "./menu1";
import menu2 from "./menu2";
import menu3 from "./menu3";
import menu4 from "./menu4";
import menu5 from "./menu5";
import menu6 from "./menu6";
import menu7 from "./menu7";
import menu8 from "./menu8";
// @ is an alias to /src

export default {
  name: "myMenu",
  components: {
    MenuList
  },
  data() {
    return {
      pageTitle: "",
      menuData: [
        //天氣概況
        {
          b1: [
            {
              name: "中央氣象局",
              url: "cwb"
            },
            {
              name: "NCDR豪雨監測",
              url: "ncdr"
            }
          ],
          b2: []
        },
        //雨量
        menu1,
        //水位
        menu2,
        //淹水感測器
        menu3,
        //即時監控
        menu4,
        //防汛工具
        menu5,
        //歷史資料
        menu6,
        //相關網站
        menu7,
        //系統管理
        menu8
      ],
      activeIndex: 0
    };
  },
  computed: {
    hasB1Menu() {
      if (!this.menuData[this.activeIndex].b1) return false;

      return this.menuData[this.activeIndex].b1.length !== 0;
    },
    b1Menu() {
      return this.menuData[this.activeIndex].b1;
    },
    hasB2Menu() {
      if (!this.menuData[this.activeIndex].b2) return false;

      return this.menuData[this.activeIndex].b2.length !== 0;
    },
    b2Menu() {
      return this.menuData[this.activeIndex].b2;
    }
  },
  methods: {
    setSubMenu(i) {
      this.activeIndex = i;
    },
    onShowPage(evt) {
      this.pageTitle = evt.name;
      if (evt.url) this.$router.push(evt.url);

      this.$bvModal.show("pageModal");
    },
    onB1ItemClick(item) {
      if (item.url) {
        this.$router.push(item.url);
        this.pageTitle = item.name;
        this.$bvModal.show("pageModal");
      } else if (item.state !== undefined) {
        item.state = !item.state;
        if (item.state && item.msg) alert(item.msg);
      }
    }
  }
};
</script>
